$(function(){
        $("a[href^='#']").click(function(){
                var _href = $(this).attr("href");
                $("html, body").animate({scrollTop: $(_href).offset().top+"px"});
                return false;
        });
});

$( document ).ready(function(){
	  $( ".submit-js" ).click(function(){ //       <button>
	    $( ".form" ).submit(); //   submit   <form>
	  });
	  $( ".form" ).submit(function( event ){ //      "submit"   <form>
	    event.preventDefault(); //       
	    $( ".contact-content__right-form-name-txt-desktop" ).text( "Form submitted" ) //      <span>
	               .css({ 
	                 "display": "inline", //  <span>   
	                 "color": "red" //   -
	               })
	               .fadeOut( 1000 ); //      <span>  1  
	});
});